/**
 * @fileoverview Generating Elixir for logic blocks.
 * @author matthew.ahrens@tufts.edu (Matthew Ahrens)
 */
'use strict';

goog.provide('Blockly.Elixir.logic');

goog.require('Blockly.Elixir');


Blockly.Elixir['controls_if'] = function(block) {
  // If/elseif/else condition.
  var n = 0;
  var argument = Blockly.Elixir.valueToCode(block, 'IF' + n,
      Blockly.Elixir.ORDER_NONE) || 'False';
  var branch = Blockly.Elixir.statementToCode(block, 'DO' + n) ||
      Blockly.Elixir.PASS;
  var code = 'cond do \n ' + argument + ' -> \n' + branch;
  for (n = 1; n <= block.elseifCount_; n++) {
    argument = Blockly.Elixir.valueToCode(block, 'IF' + n,
        Blockly.Elixir.ORDER_NONE) || 'False';
    branch = Blockly.Elixir.statementToCode(block, 'DO' + n) ||
        Blockly.Elixir.PASS;
    code += argument + ' -> \n' + branch;
  }
  if (block.elseCount_) {
    branch = Blockly.Elixir.statementToCode(block, 'ELSE') ||
        Blockly.Elixir.PASS;
    code += 'true -> \n' + branch;
  }
  code += '\n true -> :ok \n' + 'end \n'
  return code;
};

Blockly.Elixir['logic_compare'] = function(block) {
  // Comparison operator.
  var OPERATORS = {
    'EQ': '==',
    'NEQ': '!=',
    'LT': '<',
    'LTE': '<=',
    'GT': '>',
    'GTE': '>='
  };
  var operator = OPERATORS[block.getFieldValue('OP')];
  var order = Blockly.Elixir.ORDER_RELATIONAL;
  var argument0 = Blockly.Elixir.valueToCode(block, 'A', order) || '0';
  var argument1 = Blockly.Elixir.valueToCode(block, 'B', order) || '0';
  var code = argument0 + ' ' + operator + ' ' + argument1;
  return [code, order];
};

Blockly.Elixir['logic_operation'] = function(block) {
  // Operations 'and', 'or'.
  var operator = (block.getFieldValue('OP') == 'AND') ? 'and' : 'or';
  var order = (operator == 'and') ? Blockly.Elixir.ORDER_LOGICAL_AND :
      Blockly.Elixir.ORDER_LOGICAL_OR;
  var argument0 = Blockly.Elixir.valueToCode(block, 'A', order);
  var argument1 = Blockly.Elixir.valueToCode(block, 'B', order);
  if (!argument0 && !argument1) {
    // If there are no arguments, then the return value is false.
    argument0 = 'false';
    argument1 = 'false';
  } else {
    // Single missing arguments have no effect on the return value.
    var defaultArgument = (operator == 'and') ? 'true' : 'false';
    if (!argument0) {
      argument0 = defaultArgument;
    }
    if (!argument1) {
      argument1 = defaultArgument;
    }
  }
  var code = argument0 + ' ' + operator + ' ' + argument1;
  return [code, order];
};

Blockly.Elixir['logic_negate'] = function(block) {
  // Negation.
  var argument0 = Blockly.Elixir.valueToCode(block, 'BOOL',
      Blockly.Elixir.ORDER_LOGICAL_NOT) || 'true';
  var code = 'not ' + argument0;
  return [code, Blockly.Elixir.ORDER_LOGICAL_NOT];
};

Blockly.Elixir['logic_boolean'] = function(block) {
  // Boolean values true and false.
  var code = (block.getFieldValue('BOOL') == 'TRUE') ? 'true' : 'false';
  return [code, Blockly.Elixir.ORDER_ATOMIC];
};

Blockly.Elixir['logic_null'] = function(block) {
  // Null data type.
  return ['nil', Blockly.Elixir.ORDER_ATOMIC];
};

Blockly.Elixir['logic_ternary'] = function(block) {
  // Ternary operator.
  var value_if = Blockly.Elixir.valueToCode(block, 'IF',
      Blockly.Elixir.ORDER_CONDITIONAL) || 'false';
  var value_then = Blockly.Elixir.valueToCode(block, 'THEN',
      Blockly.Elixir.ORDER_CONDITIONAL) || 'nil';
  var value_else = Blockly.Elixir.valueToCode(block, 'ELSE',
      Blockly.Elixir.ORDER_CONDITIONAL) || 'nil';
  //var code = value_then + ' if ' + value_if + ' else ' + value_else;
  var code = ' if ' + value_if + ', do: ' + value_then + ', else: ' + value_else
  return [code, Blockly.Elixir.ORDER_CONDITIONAL];
};
